@import "../../breakpoint.scss";
.savings-container {
  min-height: 100vh;
  background: #f5f5f5;
  .savings {
    width: 75%;
    margin: auto;
    padding-top: 120px;
    display: flex;
    justify-content: space-between;
    @include media("<=tablet") {
      flex-direction: column-reverse;
    }
    .savings-left {
      font-size: 30px;
      font-family: "Nunito", sans-serif;
      .savings-title {
        display: flex;
        align-items: center;
        .title-line {
          width: 80px;
          height: 4px;
          border-radius: 3px;
          background-color: #60df00;
        }
        span {
          font-size: 40px;
          color: #60df00;
          margin-left: 20px;
          transition: all 0.5s ease-in-out;
          &:hover {
            font-size: 50px;
          }
        }
      }
      span {
        margin-top: 20px;
        line-height: normal;
        margin-bottom: 20px;
        color: #444444;
        font-weight: bold;
        transition: all 0.5s ease-in-out;
        &:hover {
          font-size: 32px;
        }
      }
      p {
        color: #444444;
        transition: all 0.5s ease-in-out;
        &:hover {
          font-size: 32px;
        }
      }
      button:hover {
        cursor: pointer;
      }
      button {
        width: 300px;
        height: 55px;
        border-radius: 10px;
        font-weight: bold;
        border: none;
        color: white;
        background: #60df00;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: none;
        position: relative;
        margin-top: 115px;
        font-size: 0.8em;
        padding: 15px 50px;
        overflow: hidden;
      }
      /*button:before (attr data-hover)*/
      button:hover:before {
        opacity: 1;
        transform: translate(0, 0);
      }
      button:before {
        content: url(../../assets/btn-wheel.svg);
        position: absolute;
        left: 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transform: translate(0, 100%);
        transition: all 0.3s ease-in-out;
      }
      /*button div (button text before hover)*/
      button:hover div {
        opacity: 0;
        transform: translate(0, -100%);
      }
      button div {
        text-transform: uppercase;
        letter-spacing: 3px;
        font-weight: 800;
        font-size: 0.8em;
        transition: all 0.3s ease-in-out;
      }
    }
    .savings-right {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .coin-box {
        margin-bottom: 20px;
        border-radius: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: white;
        width: 600px;
        height: 100px;
        transition: 0.5s ease;
        @include media("<=tablet") {
          width: 200px;
          background: black;
          height: 60px;
        }
        &:hover {
          transform: translate(20px, -15px);
        }
        .coin-box-left {
          margin-left: 25px;
          display: flex;
          align-items: center;
          .coin-logo {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
            height: 60px;
            border-radius: 10px;
            background-color: #dff9cc;
          }
          .coin-name {
            margin-left: 15px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            span {
              font-size: 14px;
              color: #959595;
              font-weight: bold;
              &:first-child {
                color: #444444;
                font-size: 18px;
              }
            }
          }
        }
        .coin-box-right {
          margin-right: 25px;
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          span {
            font-size: 14px;
            color: #959595;
            font-weight: bold;
            &:first-child {
              font-size: 18px;
              color: #60df00;
            }
          }
        }
        .coin-box-center {
          margin-right: 20px;
          display: flex;
          flex-direction: column;
          align-items: flex-middle;
          span {
            font-size: 14px;
            color: #959595;
            font-weight: bold;
            &:first-child {
              font-size: 18px;
              color: #60df00;
            }
          }
        }
      }
      button:hover {
        cursor: pointer;
      }
      button {
        color: #444444;
        background-color: #dbdbdb;
        margin-top: 10px;
        border: none;
        width: 300px;
        height: 55px;
        border-radius: 10px;
        margin-bottom: 200px;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: none;
        position: relative;
        overflow: hidden;
        @include media("<=tablet") {
          margin-bottom: 50px;
        }
      }
      /*button:before (attr data-hover)*/
      button:hover:before {
        opacity: 1;
        transform: translate(0, 0);
      }
      button:before {
        content: url(../../assets/three-dots.svg);
        position: absolute;
        left: 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transform: translate(0, 100%);
        transition: all 0.3s ease-in-out;
      }
      /*button div (button text before hover)*/
      button:hover div {
        opacity: 0;
        transform: translate(0, -100%);
      }
      button div {
        text-transform: uppercase;
        letter-spacing: 3px;
        font-size: 0.8em;
        transition: all 0.3s ease-in-out;
      }
    }
    .coin-box-right1 {
      margin-right: 25px;
      margin-top: 0px;
      margin-bottom: 25px;
      display: flex;
      flex-direction: column;
      align-items: flex-center;
      span {
          font-size: 8px;
          color: #959595;
          font-weight: bold;
          &:first-child {
              font-size: 10px;
              color: #232422;
          }
      }
  }
  }
}
